.FinisfedQuiz {
    padding: 20px;
    border: 2px solid #fff;
    border-radius: 5px;
    margin: 0 10px;
}
.FinisfedQuiz ul {
    list-style: none;
    padding: 0;
    margin: 0;
}
.FinisfedQuiz ul li {

}
.FinisfedQuiz ul li i {
    margin-left: 10px;
}
.FinisfedQuiz ul li i.success {
    color: rgba(161, 240, 69, .7);
}
.FinisfedQuiz ul li i.error {
    color: rgba(240, 87, 108, .7);
}